.option_block {
    position: fixed;
    display: flex;
    flex-direction: column;
    top: 100px;
    left: 0;
    padding:  10px 5px;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.48);
    transform: translateX(-50px);
    transition: 0.4s ease-in-out;
}
.option_active {
    transition: 0.4s ease-in-out;
    transform: translateX(0);
}
.open_menu_btn {
    margin: 5px;
    width: 40px;
    height: 40px;
    border-radius: 40px;
    border: none;
    background-color: #ffffff;
    position: fixed;
    left: 0;
    top: 50px;
    background-image: url("../../images/plusIcon.png");
    transition: 0.4s ease-in-out;
}
.menu_btn_opened {
    transform: rotate(135deg);
}
button img {
    width: inherit;
    height: inherit;
}

.visible_btn {
    font-weight: bolder;
    font-size: 10px;
    background: white;
    color: black;
    width: 30px;
    height: 30px;
    border: none;
    /*border: 1px solid lightgray;*/
    border-radius: 15px;
    margin: 5px;
    cursor: pointer;
}
.visible_btn_active {
    background-color: #000000;
    color: white;
}
.shuffle_btn:hover {
    animation: rotate_arrow  0.5s running ease-in-out;
}






@keyframes rotate_arrow {
    from {
        transform: rotateX(0deg);
    }
    25% {
        transform: rotateX(90deg) ;
    }
    50% {
        transform: rotateX(180deg) ;
    }
    75% {
        transform: rotateX(270deg) ;
    }
    to {
        transform: rotateX(360deg);
    }
}

@media (max-width: 600px) {
    .open_menu_btn {
        top: 0;
    }
    .option_block {
        top: 50px;
    }
}




